.page {
  height: 100%;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 15px;
  line-height: 24px;
}

.page__body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: $main-background;
  color: $heading-color;
}

img {
  width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  text-decoration: none;
  display: flex;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 260px;
  height: 52px;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  color: $text-color;
  border: 2px solid #444444;
}

.container {
  min-width: 260px;
  padding: 0 30px;

  @media (min-width: $tablet-width) {
    min-width: 668px;
    padding: 0 50px;
  }

  @media (min-width: $desktop-width) {
    min-width: 1050px;
    padding: 0 50px;
  }
}
